<template>
  <div class="wrapper">
    <Navbar @setSearch="searchValue = $event"/>
    <Notes 
    :notes="filterNotes"
    @delNote="delNote"
    @edit="edit"
    />
    <Modal
      v-show="isModalOpen"
      @close="isModalOpen = false"
      @addNote="addNote"
      :editNote="editNote"
      :currentNote="currentNote"
      @changeNote="changeNote"
      :titles="titles"
    />
    <button class="add__not" @click="isModalOpen = true, editNote = false">
      <img src="@/assets/img/edit.svg" alt="" />
    </button>
  </div>
</template>

<script>
import Navbar from "@/components/Navbar.vue";
import Notes from "@/components/Notes.vue";
import Modal from "@/components/Modal.vue";
export default {
  components: {
    Navbar,
    Notes,
    Modal,
  },
  data() {
    return {
      notes: [],
      isModalOpen: false,
      editNote: false,
      currentNote: null,
      searchValue: '',
      titles: ''
    };
  },
  mounted() {
    this.getNotes();
  },
  methods: {
    addNote(note) {
      this.notes.push(note);
    },
    getNotes() {
      let localNote = localStorage.notes;
      if (localNote) {
        this.notes = JSON.parse(localNote);
      }
    },
    delNote(id){
      let index = this.notes.findIndex(note => note.id == id)
      this.notes.splice(index, 1)
    },
    edit(id, title){
     this.isModalOpen = this.editNote = true
     let current = this.notes.find(note => note.id == id)
     this.currentNote = current
     this.titles = title
    },
  
    changeNote(newNote){
      let index = this.notes.findIndex(note => note.id == newNote.id)
      this.notes[index] = newNote
    }
  },
  computed:{
    filterNotes(){
      return this.searchValue ? this.notes.filter(note => note.title.toLowerCase().includes(this.searchValue.toLowerCase())) : this.notes
    }
  },
  watch: {
    notes: {
      handler() {
        localStorage.notes = JSON.stringify(this.notes);
      },
      deep: true,
    },
  },
};
</script>

