<template>
    <div class="notes__item">
        <div class="notes__item-top" :class="{ active: !show }">
            <h2 class="notes__item-top-title">{{ note.title }}</h2>
            <p class="notes__item-top-date">{{ note.date }}</p>
        </div>
        <p class="notes__item-text">{{ note.text }}</p>
        <div class="notes__item-btns">
            <button class="notes__item-btns-btn edit" @click="$emit('edit', note.id, note.title)">
                <img src="@/assets/img/edit.svg" alt="">
                <span>Редактировать</span>
            </button>
            <button class="notes__item-btns-btn del" @click="$emit('delNote', note.id)">
                <img src="@/assets/img/del.svg" alt="">
                <span>Удалить</span>
            </button>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        note: {
            typeof: Object
        },
        show: {
            typeof: Boolean
        }
    }
}
</script>

<style lang="scss" scoped></style>