<template>
    <main class="main">
        <section class="notes">
            <div class="container">
                <div class="notes__top">
                    <h2 class="notes__top-title">Все заметки</h2>
                    <button class="notes__top-btn" @click="show = !show">
                        <img v-show="show" src="@/assets/img/list.svg" alt="" />
                        <img v-show="show == false" src="@/assets/img/grid.svg" alt="" />
                        <span>{{ show ? 'Список' : 'Сетка' }}</span>
                    </button>
                </div>
                <div class="notes__list" :class="{ active: !show }">
                    <NotesItem 
                    v-for="note in notes" 
                    :key="note.id" :note="note" 
                    :show="show" 
                    @delNote="$emit('delNote', note.id)"
                    @edit="$emit('edit', note.id, note.title)"
                    />
                </div>
            </div>

        </section>
    </main>
</template>

<script>
import NotesItem from './NotesItem.vue'
export default {
    props: {
        notes: {
            typeof: Array
        }
    },
    components: {
        NotesItem
    },
    data() {
        return {
            show: true
        }
    }
};
</script>

<style lang="scss" scoped></style>