<template>
  <Transition name="modal">
    <div class="modal" @click="closeModal">
      <div class="modal__block" @click.stop="">
        <h2 class="modal__block-title">
          {{ editNote ? 'Изменить заметку' : 'Добавить заметку' }}
        </h2>
        <div class="modal__block-inputs">
          <label for="inp">
            <span>Title</span>
            <input type="text" id="inp" v-model="title" />

          </label>
          <label for="area">
            <span>Content</span>
            <textarea id="area" v-model="text"></textarea>
          </label>
        </div>
        <div class="modal__block-btns">
          <button class="modal__block-btns-btn del" @click="closeModal">
            Отмена
          </button>
          <button v-if="!editNote" class="modal__block-btns-btn edit" @click="addNote">
            Добавить
          </button>
          <button v-else class="modal__block-btns-btn edit" @click="changeNote">
            Изменить
          </button>
        </div>
      </div>
    </div>
  </Transition>
</template>

<script>
import { v4 as uuidv4 } from 'uuid';
export default {
  props:{
    editNote: {
      typeof: Boolean 
    },
    currentNote:{
      typeof: Object
    },
    titles:{
      typeof: String
    }
  },
  data() {
    return {
      title: this.titles,
      text: "",
    };
  },
  methods: {
    closeModal() {
      this.$emit("close");
      this.title = this.text = "";
    },
    addNote() {
      if (this.title != '' && this.text != '') {
        const note = {
          id: uuidv4(),
          title: this.title,
          text: this.text,
          date: new Date().toLocaleDateString(),
        };
        this.$emit("addNote", note);
        this.closeModal()
      }
    },
    changeNote(){
       if (this.title != '' && this.text != '') {
        const note = {
          id: this.currentNote.id,
          title: this.title,
          text: this.text,
          date: new Date().toLocaleDateString(),
        };
        this.$emit("changeNote", note);
        this.closeModal()
      }
    }
  },
 
};
</script>

<style lang="scss" scoped>
.modal-enter-active,
.modal-leave-active {
  transition: all 0.3s ease;
}

.modal-enter-from,
.modal-leave-to {
  opacity: 0;
  transform: scale(1.5);
}
</style>