<template>
  <header class="header">
    <nav class="header__nav" v-if="nav">
      <button class="header__nav-lang" @click="switchLang">{{ $i18n.locale }}</button>
      <h1 class="header__nav-title">{{ $t('notes') }}</h1>
      <button class="header__nav-search" @click="nav = false">
        <img src="@/assets/img/search.svg" alt="" />
      </button>
    </nav>
    <nav class="header__search" v-else>
      <button class="header__search-back" @click="nav = true">
        <img src="@/assets/img/back.svg" alt="" />
      </button>
      <input 
      type="text" 
      :placeholder="$t('search')" 
      class="header__search-input"
      v-model="search"
      />
      <button class="header__search-clear">
        <img src="@/assets/img/clear.svg" alt="" />
      </button>
    </nav>
  </header>
</template>

<script>
export default {
  data() {
    return {
      nav: true,
      search: ''
    };
  },
  watch:{
    search(newVal){
      this.$emit('setSearch', newVal)
    },
    
  },
  methods:{
    switchLang(){
      this.$i18n.locale == 'ru' ? this.$i18n.locale = 'uz' : this.$i18n.locale = 'ru'
      localStorage.lang = this.$i18n.locale
    }
  }
};
</script>

